import React, { Component } from "react";
import axios from "axios";
import { Skeleton, Box, Card, CardMedia, Grid } from "@mui/material";
import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/lazy"
import "swiper/css/navigation"
import "swiper/css/pagination"

import SwiperCore, { Lazy, Pagination } from 'swiper';

SwiperCore.use([Lazy, Pagination]);

class AppOurPartner extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ourPartnerList: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        this.ourPartnerData().then(r => {
            this.setState({ isLoaded: true, ourPartnerList: r.data });
        });
    }

    ourPartnerData = async () => {
        return await axios.get("/api/ourpartner/");
    };

    render() {
        const slides = [];
        const { isLoaded, ourPartnerList } = this.state;
        if (isLoaded) {
            ourPartnerList.map((item, index) => (
                slides.push(
                    <SwiperSlide key={`slide-${index}`}>
                        <Card className="w-100 h-100">

                            <Box>
                                <CardMedia
                                    component="img"
                                    alt={item.title}
                                    style={{ maxHeight: "100px", objectFit: "scale-down" }}
                                    image={item.logo}
                                    title={item.title}
                                    className="swiper-lazy"
                                />
                                <div className="swiper-lazy-preloader swiper-lazy-preloader-black" />
                            </Box>
                        </Card>
                    </SwiperSlide>
                )
            ));
        }
        if (!isLoaded) {
            return (
                <Grid container spacing={4}>
                    {Array.from(new Array(4)).map((el, i) =>
                        <Grid item xs={12} sm={6} md={3} key={i}>
                            <Box sx={{ pt: 0.5 }}>
                                <Skeleton variant="rectangular" width="100%" height={100} />
                            </Box>
                        </Grid>
                    )}
                </Grid>
            )
        } else {
            return (
                ourPartnerList.length > 0 ? (
                    <Swiper dir="rtl" lazy={true} pagination={{
                        "type": "progressbar"
                    }} slidesPerView={1} spaceBetween={30} breakpoints={{
                        // when window width is >= 320px
                        320: {
                            slidesPerView: 2, spaceBetween: 10
                        }, // when window width is >= 640px
                        640: {
                            slidesPerView: 2, spaceBetween: 10
                        }, // when window width is >= 768px
                        768: {
                            slidesPerView: 2, spaceBetween: 10
                        }, // when window width is >= 1024px
                        1024: {
                            slidesPerView: 4, spaceBetween: 10
                        },
                    }} className="our-partner-swiper p-1">
                        {slides}
                    </Swiper>
                ) : ('')
            );
        }
    }
}

export default AppOurPartner;