import React, { Component } from "react";
import AppPageContact from "./PageContact/AppPageContact";
import AppOurPartner from "./OurPartner/AppOurPartner";
import Container from '@mui/material/Container';
import Box from "@mui/material/Box";
import { ContactSupport } from '@mui/icons-material';
import axios from 'axios';
import SiteSection from "./SiteSection";
import { emphasize, styled } from '@mui/material/styles';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Chip from '@mui/material/Chip';
import HomeIcon from '@mui/icons-material/Home';

const StyledBreadcrumb = styled(Chip)(({ theme }) => {
    const backgroundColor =
        theme.palette.mode === 'light'
            ? theme.palette.grey[100]
            : theme.palette.grey[800];
    return {
        backgroundColor,
        height: theme.spacing(3),
        paddingRight: 15,
        color: theme.palette.text.primary,
        fontWeight: theme.typography.fontWeightRegular,
        '&:hover, &:focus': {
            backgroundColor: emphasize(backgroundColor, 0.06),
        },
        '&:active': {
            boxShadow: theme.shadows[1],
            backgroundColor: emphasize(backgroundColor, 0.12),
        },
    };
});

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            siteSections: [],
            isLoaded: false,
        };
    }

    componentDidMount() {
        this.sectionData();
    }

    sectionData = () => {
        axios.get('/api/sitesections/').then((res) => {
            this.setState({ isLoaded: true, siteSections: res.data });
        }).catch((err) => console.log(err));
    };

    render() {
        const { siteSections, isLoaded } = this.state;
        const sections = {};
        if (isLoaded) {
            siteSections.forEach((section) => {
                sections[section.section_type] = section;
            });
        }

        return (
            <div>
                <Box
                    sx={{
                        width: '100%',
                        height: 20,
                    }}
                />
                <Breadcrumbs aria-label="breadcrumb" className={'m-2'}>
                    <StyledBreadcrumb
                        component="div"
                        href="#"
                        label="الرئيسية"
                        icon={<HomeIcon fontSize="small" />}
                    />
                    <StyledBreadcrumb
                        component="div"
                        href="#"
                        label="تواصل معنا"
                        icon={<ContactSupport fontSize='small' />}
                    />
                </Breadcrumbs>
                <Container component='div'>
                    <AppPageContact />
                    <div className='mt-1'>
                        {sections.ourpartner ? (
                            <SiteSection
                                title={sections.ourpartner.title}
                                subtitle={sections.ourpartner.subtitle}
                                description={sections.ourpartner.description}
                            />
                        ) : ''}
                        <AppOurPartner />
                    </div>
                </Container>
            </div>
        );
    }
}

export default Contact;